<template>
    <p-card style="width: 800px; margin: auto">
        <template #content>
            <div class="p-grid">
                <div class="p-col">
                    <p-button
                        label="Users"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToUsers"
                    />
                </div>
                <div class="p-col">
                    <p-button
                        label="Drug Inventory"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToDrugInventory"
                    />
                </div>
                <div class="p-col">
                    <p-button
                        label="Diagnosis List"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToDaignosisInventory"
                    />
                </div>
                <div class="p-col">
                    <p-button
                        label="Born"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToBorn"
                    />
                </div>
                <div class="p-col">
                    <p-button
                        label="User Groups"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToUserGroups"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
    setup() {
        const router = useRouter();
        const store = useStore();
        return {
            goToUsers: () => {
                router.push({ path: "/maintanance/users/" });
            },
            goToDrugInventory: () => {
                router.push({ path: "/maintanance/drug-inventory/" });
            },
            goToDaignosisInventory: () => {
                router.push({ path: "/maintanance/diagnosis-inventory/" });
            },
            goToBorn: () => {
                const username = store.state.user.username;
                const patientId = store.state.artCycle.patient.id;
                const url = `http://172.16.0.64:8080/born/externalLogin?uid=${username}&pId=${patientId}`;
                window.open(url);
            },
            goToUserGroups: () => {
                router.push({ path: "/maintanance/groups/" });
            }
        };
    }
};
</script>
